  .hide {
    display: none !important;
    transition: all 5ms ease-in-out;
  }

  .fade-in {
    opacity: 1;
    transition: opacity 5s ease-in;
  }


  .show {
    display: block !important;
  }

  .btn-send-gift {
    text-align: center;
    position: fixed;
    bottom: 45px;
    z-index: 99;
    right: 25px;
    display: flex;
    align-items: center;
    width: -moz-fit-content;
    width: fit-content;
  }

  .nav {
    position: fixed;
    transform: translate(-50%, -50%);
    left: 50%;
    z-index: 2;
    top: 30%;
  }

  .nav.ver2 {
    right: calc(50% - 300px);
  }

  .nav ul {
    list-style: none;
    display: flex;
    flex-direction: column;
  }

  .nav ul li:not(:last-child) {
    margin-bottom: 10px;
  }

  .nav a:hover span.text {
    display: inline-block;
  }

  .nav a {
    margin-bottom: 10px;
  }

  .nav a:hover {
    transform: scale(1.1);
    transition: transform 5ms ease-in-out;
  }

  .nav a img {
    width: 40px;
  }

  .nav .navItem.active img {
    transform: scale(1.1);
  }

  .demo-top-notif {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7411764706);
    z-index: 99;
    height: 50px;
    line-height: 50px;
    color: #fff;
    padding: 0 25px;
    flex-wrap: wrap;
  }

  .demo-top-notif a {
    padding: 6px 15px;
    text-align: center;
    border: 1px solid #ffffff;
    border-radius: 5px;
    color: #ffffff;
  }

  .modal {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1024;
    top: 0;
    left: 0;
  }

  .modal.show {
    display: block;
  }

  .modal.show body {
    overflow: hidden;
  }

  .modal .modal-content {
    width: 600px;
    max-height: 600px;
    margin: auto;
    box-shadow: 1px 3px 29px 2px rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: 1px 3px 29px 2px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 1px 3px 29px 2px rgba(0, 0, 0, 0.2);
    background: #fff;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: fixed;
    transition: all 400ms ease;
    border-radius: 7px;
  }

  .modal .modal-content.modal-sm {
    width: 400px;
  }

  .modal .modal-content .body {
    position: relative;
    max-height: 600px;
    overflow-y: auto;
  }

  .modal .modal-content .close-modal {
    height: 40px;
    width: 40px;
    background-color: #b94239;
    border-radius: 50%;
    position: absolute;
    right: 10px;
    top: -20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    z-index: 99;
  }

  section.modal-send-gift {
    padding: 25px 0;
  }

  section.modal-send-gift .content {
    max-height: 500px;
    overflow-y: auto;
    scroll-behavior: smooth;
  }

  section.modal-send-gift .content::-webkit-scrollbar {
    display: none;
  }

  section.modal-send-gift h3 {
    font-size: 1.3rem;
    font-family: sans-serif;
    color: #607c8a;
  }

  section.modal-send-gift ul {
    list-style: none;
    width: 100%;
    margin-bottom: 30px;
  }

  section.modal-send-gift ul li .item {
    padding: 15px;
    box-shadow: 2px 2px 11px 3px rgba(183, 183, 183, 0.25);
    border-radius: 10px;
    overflow: hidden;
  }

  section.modal-send-gift ul li .item label {
    text-transform: uppercase;
    font-size: 0.6rem;
    letter-spacing: 3px;
    color: #b0b4b9;
  }

  section.modal-send-gift ul li .item .value {
    font-size: 1.1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #617c8a;
  }

  section.modal-send-gift ul li .item .value.account-number {
    font-size: 1.3rem !important;
  }

  section.modal-send-gift ul li .item .value input {
    height: 35px;
    padding: 0 7px;
    border: 1px solid #617c8a;
    flex: 3;
    margin-right: 5px;
    width: 100%;
    border-radius: 5px;
    color: #617c8a;
  }

  section.modal-send-gift ul li .item .value a.copy {
    background: #607c8a;
    padding: 6px 20px;
    color: #fff;
    border-radius: 5px;
    text-align: center;
    flex: 1;
  }

  .splide__pagination__page {
    width: 8px;
    height: 8px;
    border: none;
    border-radius: 50%;
    margin-right: 10px;
    cursor: pointer;
  }

  .splide__pagination__page.is-active {
    background-color: #a2a2a2;
  }

  .splide__arrow--prev {
    position: absolute;
    top: 50%;
    left: 0;
    transform: scaleX(-1) translateY(-50%);
    border-radius: 50%;
    padding: 10px;
    background: transparent;
    border: 2px solid #808080;
    z-index: 99;
    cursor: pointer;
  }

  .splide__arrow--prev svg {
    width: 30px !important;
    height: 30px !important;
    fill: #808080 !important;
  }

  .splide__arrow--next {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    border-radius: 50%;
    padding: 10px;
    background: transparent;
    border: 2px solid #808080;
    z-index: 99;
    cursor: pointer;
  }

  .splide__arrow--next svg {
    width: 30px !important;
    height: 30px !important;
    fill: #808080 !important;
  }

  .flex-wrap {
    flex-wrap: wrap;
  }

  .flex-no-wrap {
    flex-wrap: nowrap;
  }

  @keyframes pulse {
    0% {
      box-shadow: 0 0 0 0 rgba(102, 103, 101, 0.4);
    }

    70% {
      box-shadow: 0 0 0 10px rgba(102, 103, 101, 0);
    }

    100% {
      box-shadow: 0 0 0 0 rgba(102, 103, 101, 0.4);
    }
  }

  .list-gift-product {
    list-style: none;
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
    padding-left: 0 !important;
  }

  .list-gift-product li {
    width: calc(50% - 2px);
    margin: 1px 1px 20px 1px;
    position: relative;
    padding: 0px 0px 50px 0px;
    border: 1px solid #c0c0c0;
    border-radius: 5px;
  }

  .list-gift-product li .image img {
    width: 100%;
    border-radius: 3px 3px 0 0;
  }

  .list-gift-product li .info {
    padding: 0 10px;
  }

  .list-gift-product li .info .product-title {
    display: block;
    font-size: 0.9rem;
    line-height: 20px;
    color: #617c8a;
  }

  .list-gift-product li .info .product-price {
    display: block;
    font-size: 0.9rem;
    line-height: 20px;
    color: #df5f5a;
    font-weight: 600;
  }

  .list-gift-product li .product-action {
    width: calc(100% - 10px);
    position: absolute;
    bottom: 5px;
    left: 5px;
    display: flex;
  }

  .list-gift-product li .product-action a {
    border: 1px solid;
    padding: 4px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.9rem;
    width: 100%;
    border-radius: 5px;
    background: #607c8a;
    color: #fff;
  }

  @media (min-width: 320px) and (max-width: 480px) {
    .nav {
      position: fixed;
      bottom: 10px;
      right: auto;
      top: auto;
      transform: translate(-50%);
      z-index: 99;
      width: 100%;
    }

    .nav.ver2 {
      right: auto;
    }

    .nav ul {
      margin: auto;
      display: flex;
      justify-content: center;
      list-style: none;
      width: 90%;
      flex-direction: row;
    }

    .nav ul li:not(:last-child) {
      margin-right: 15px;
    }

    .nav ul li img {
      width: 50px;
      height: 50px;
      -o-object-fit: cover;
      object-fit: cover;
    }

    .btn-send-gift {
      bottom: 105px;
      left: 50% !important;
      transform: translate(-50%);
    }

    .row {
      flex-direction: column;
    }

    .row .col {
      width: 100%;
      margin: auto;
    }

    .demo-top-notif {
      padding: 0 10px;
    }

    .modal-content {
      width: 90% !important;
    }

    .modal-content .body {
      position: relative;
      max-height: 600px !important;
      overflow-y: auto;
    }

    .splide__list {
      margin-left: 0px !important;
    }

    #audio_control {
      top: unset;
      left: 20px;
      right: unset;
      bottom: 100px;
    }

    .list-gift-product li {
      width: calc(50% - 2px);
      margin-bottom: 7px;
    }

    .list-gift-product li .info {
      padding: 5px 10px 0 5px;
    }

    .list-gift-product li.add-item {
      padding: 3px;
      position: relative;
      min-height: 65px;
      width: 100%;
      background-size: cover;
    }

    .list-gift-product li.add-item a {
      font-size: 12px;
    }
  }

  @media screen and (max-width: 375px) {
    .demo-top-notif div {
      font-size: 0.8rem;
    }

    .demo-top-notif a {
      font-size: 0.8rem;
    }

    .list-gift-product li .info .product-title {
      font-size: 0.8rem;
    }

    .list-gift-product li .info .product-price {
      font-size: 0.75rem;
    }
  }

  #confirmation_form_gift label {
    display: block;
  }

  #confirmation_form_gift input,
  #confirmation_form_gift select {
    height: 40px;
    width: 100%;
    border: 1px solid #2b2b2b;
    color: #2b2b2b;
    padding: 0 10px;
    border-radius: 5px;
  }

  #btn_cancel_confirm,
  #btn_submit_confirm {
    padding: 7px 25px;
    text-align: center;
    border-radius: 30px;
  }

  #btn_cancel_confirm {
    border: 1px solid #b94239;
    color: #b94239;
  }

  #btn_submit_confirm {
    background: #b94239;
    color: #ffffff;
  }

  #btn_message,
  #btn_send_gift {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
  }

  #btn_message img,
